.StartContainer {
  height: 100vh;
  width: 100vw;
  font-family: Papyrus, fantasy;
}
.StartContainer .StartBody {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 95%;
}
.StartContainer .StartBody .StartTitle {
  font-size: 6rem;
}
.StartContainer .StartBody .StartButton {
  border: 0;
  width: 8rem;
  background-color: black;
  color: white;
  font-size: 2rem;
  font-family: Papyrus, fantasy;
  border-radius: 25rem;
}
.StartContainer .StartBody .StartButton:hover {
  cursor: pointer;
  background-color: white;
  color: black;
}
.StartContainer .StartFooter {
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding-left: 1rem;
  font-size: 1.5rem;
}



